export class Utils {
    static calculateWinner(squares) {
        const lines = [
            [0, 1, 2],
            [3, 4, 5],
            [6, 7, 8],
            [0, 3, 6],
            [1, 4, 7],
            [2, 5, 8],
            [0, 4, 8],
            [2, 4, 6],
        ];
        for (let i = 0; i < lines.length; i++) {
            const [a, b, c] = lines[i];
            if (squares[a] && squares[b] && squares[c]) {
                if (squares[a].val === squares[b].val && squares[a].val === squares[c].val) {
                    return {
                        val: squares[a].val,
                        squares: [squares[a], squares[b], squares[c]]
                    };
                }
            }
        }
        return null;
    }

    static calcCellCoordinates(cell) {
        cell++;
        return {
            x: cell % 3 || 3,
            y: Math.ceil(cell / 3)
        };
    }
}


